<template>
  <el-form class="password-form">
    <!-- 编辑用户 -->
    <el-form-item label="当前密码" prop="oldPassword">
      <el-input v-model=" resetForm.oldPassword" auto-complete="off" type="password"></el-input>
    </el-form-item>
    <el-form-item label="新密码" prop="newPassword">
      <el-input v-model=" resetForm.newPassword" auto-complete="off" type="password"></el-input>
    </el-form-item>
    <el-form-item label="重复新密码" prop="repeatPassword">
      <el-input v-model=" resetForm.repeatPassword" auto-complete="off" type="password"></el-input>
    </el-form-item>

    <el-button @click="saveEditDialog('ruleEditForm')" class="dialog_button" type="primary">提交</el-button>

  </el-form>

</template>

<script>
import qs from "qs";
import { getValue, changeTree, difference, extend } from "@/util/util.js";
export default {
  name: "userOperation",
  components: {},
  data() {
    return {
      //重置用户密码
      userInfo: {},
      loginForm: {
        account: "",
        password: ""
      },
      resetForm: {
        oldPassword: "",
        newPassword: "",
        repeatPassword: ""
      },
      resetRules: this.eachValidate({
        password: { required: true, account: true },
        newPassword: { required: true, account: true },
        repeatPassword: { required: true, account: true }
      })
    };
  },
  methods: {
    saveEditDialog() {
      //请求当前用户信息
      if (this.userInfo == null) {
        this.requesetUsers();
      }
      //判断新密码输入是否正确
      if (this.resetForm.newPassword !== this.resetForm.repeatPassword) {
        alert("新密码输入不正确");
        this.resetForm.newPassword = this.resetForm.repeatPassword = "";
        return;
      }
      this.loginForm.account = this.userInfo.account;
      this.loginForm.password = this.resetForm.oldPassword;
      //判断旧密码是否正确,更改新密码
      this.getUser();
    },
    //请求当前用户信息
    requesetUsers() {
      return this.$ajax({
        method: "get",
        url: "rest/user/currentUser",
        params: {}
      })
        .then(res => {
          if (res.data.status) {
            this.userInfo = res.data.records;
          }
        })
        .catch(err => {});
    },
    //根据用户名和密码是否正确
    getUser() {
      this.$ajax({
        url: "rest/logLogin/login",
        method: "post",
        data: this.loginForm,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        transformRequest: [
          function(data) {
            return qs.stringify(data);
          }
        ]
      }).then(res => {
        if (res.data.status) {
          this.resetPassword();
        } else {
          alert("输入旧密码错误");
          this.resetForm.oldPassword = "";
          return;
        }
      });
    },
    //修改密码
    resetPassword() {
      this.userInfo.password = this.resetForm.newPassword.replace(/\s*/g, "");
      this.$ajax({
        method: "put",
        url: "rest/user",
        data: this.userInfo
      })
        .then(res => {
          if (res.data.status) {
            this.$message({
              message: res.data.answerMsg,
              type: "success"
            });
          } else {
            this.$message.error(res.data.answerMsg);
          }
        })
        .catch(err => {
          console.log(err, "编辑错误");
        });
    }
  },
  mounted() {
    this.requesetUsers();
  }
};
</script>

<style scoped>
.password-form {
  width: 500px;
  margin: 30px auto 10px;
}
</style>





